body {
  background-color: black;
  padding: 20px;
  margin: 0;
}
h1, h2, h4, p, ul, li, i {
  font-family: monospace;
  text-align: center;
}

p, i {
  font-size: 18px;
}

ul.header li {
  display: inline;
  list-style-type: none;
  margin: 0;
}
ul.header {
  background-color: #111;
  padding: 0;
}
ul.header li a {
  color: #FFF;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
}
.content {
  background-color: #FFF;
  padding: 20px;
}
.content h2 {
  padding: 0;
  margin: 0;
}
.content li {
  margin-bottom: 10px;
}

#demoday {
  text-align: center;
}